$yellow: #ffc600;

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; }

  @-moz-keyframes #{$name} {
    @content; }

  @keyframes #{$name} {
    @content; } }

@mixin animate($name, $duration, $timing-function, $iteration) {
  -webkit-animation: $name $duration $timing-function $iteration;
  -moz-animation: $name $duration $timing-function $iteration;
  animation: $name $duration $timing-function $iteration; }

@include keyframes(play) {
  0% {
    transform: scale(1);
    border-color: black; }
  50% {
    transform: scale(1.2);
    border-color: $yellow; }
  100% {
    transform: scale(1);
    border-color: black; } }

@mixin player {
  @include animate(play, 0.3s, linear, 1); }

body {
  background-image: url("background.jpeg");
  background-size: cover;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  min-height: 100vh;
  overflow: hidden; }

.key {
  text-align: center;
  margin: 1rem;
  border-radius: 5px;
  padding: 10px;
  border: 4px solid black;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
  display: inline-block;
  width: 81px; }

kbd {
  display: block;
  font-size: 2em;
  font-family: 'Bree Serif', serif; }

span {
  font-size: 0.7em;
  color: $yellow; }

.description {
  color: $yellow;
  margin-top: 5px; }

.playing {
  @include player; }
